@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,600;1,600&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

input{-webkit-appearance:none;-moz-appearance:none;appearance:none;outline:none;border:none}.inputGroup{margin:0 0px 20px;padding:0;border-style:none;box-shadow:0 6px 9px rgba(50,50,93,0.06),0 2px 5px rgba(0,0,0,0.35);background-color:white;border-radius:4px}.inputGroup .FormRow{display:flex;margin:0 15px}.inputGroup .FormRow .FormRowLabel{display:flex;align-items:center;width:15%;min-width:90px;padding:11px 20px 11px 0;font-weight:600;margin-bottom:0}.inputGroup .FormRow .FormRowInput{font-size:16px;width:100%;padding:9px 25px 11px 0;color:#000;border-radius:4px}

.tile{border-style:none;box-shadow:0 2px 5px rgba(0,0,0,0.1);margin:10px 0}

.container{padding:0 10vw}

